<template>
  <el-card>
    <main-header title="" slot="header" />
    <div class="container">
      <h3>123</h3>
      <el-badge :value="10000">
        <el-button type="primary">
          <span class="red"></span>
          
        </el-button>
      </el-badge>
      <el-badge :value="12">
        <el-button type="primary">
          <span class="red"></span>
          
        </el-button>
      </el-badge>
    </div>
    <div class="chartContainer">
      <div ref="chart" :style="{width:'400px',height:'300px'}" />
      <div>
        <div v-for="({label,num=0,percent=0,color}, key) in subjectInfo" :key="key" class="percent">
          <div>{{label}}</div>
          <el-progress :text-inside="true" :stroke-width="15" :percentage="percent" :color="color" :show-text="false">
          </el-progress>
          <div>{{`${num} / ${percent}%`}}</div>
        </div>
      </div>
    </div>
    <div class="tableHeader">
      <span>({{300}})</span>
      <el-button size="medium" type="primary"></el-button>
    </div>
    <el-table :data="researchDetailInfo">
      <el-table-column prop="id" label="ID" align="center" />
      <el-table-column prop="name" label="" align="center" />
      <el-table-column :render-header="classRender" prop="class" align="center" />
      <el-table-column v-for="(item, index) in subjectColumns" :key="index" :label="item" align="center">
        <span slot-scope="{row}">
          {{row.subject.find(ele => ele === item) ? item : ''}}
        </span>
      </el-table-column>
    </el-table>
    <el-pagination layout="prev, pager, next, jumper" :total="total" :page-size="20" @current-change="pageChangeHandle" />
  </el-card>
</template>
<script>
import chart from '@/etc/echarts'

export default {
  data() {
    return {
      total: 1,
      pageSize: 1,

      classVal: 0,

      researchDetailInfo: [],
      subjectInfo: [
        { label: '', num: 45, percent: 90, color: '#66ccff' },
        { label: '', num: 45, percent: 90, color: '#66ccff' },
        { label: '', num: 45, percent: 90, color: '#66ccff' },
        { label: '', num: 45, percent: 90, color: '#66ccff' },
        { label: '', num: 45, percent: 90, color: '#66ccff' },
        { label: '', num: 45, percent: 90, color: '#66ccff' },
      ],
      subjectColumns: [],
      classOptions: [''],
      classRender: (createElement) => createElement(
        'el-dropdown', {},
        [
          createElement('el-button', {}, [
            createElement('span', {
              domProps: { innerText: '' },
            }),
            createElement('i', { class: 'el-icon-arrow-down el-icon--right' }),
          ]),
          createElement('el-dropdown-menu', { slot: 'dropdown' },
            this.classOptions.map((ele, index) => createElement(
              'el-dropdown-item', {
                on: { command: this.selectClass },
                props: { command: index },
                domProps: { innerHTML: ele },
              },
            ))),
        ],
      ),
    }
  },
  methods: {
    pageChangeHandle(pageIndex) {
      this.pageIndex = pageIndex
    },
    selectClass(val) {
      console.log(val)
    },
  },
  mounted() {
    chart.then(([echarts]) => {
      this.chart = echarts.init(this.$refs.chart)
      this.chart.setOption({
        title: {
          text: '',
          left: 'center',
          textStyle: {
            fontWeight: 'normal',
          },
        },
        series: [{
          name: '',
          type: 'pie',
          data: [
            { value: 20, name: '' },
            { value: 80, name: '' },
            { value: 40, name: '' },
            { value: 50, name: '' },
            { value: 70, name: '' },
            { value: 80, name: '' },
          ],
          label: {
            normal: {
              formatter: ({ name, percent }) => `${name}:${percent}%`,
            },
          },
          hoverAnimation: false,
        },
        ],
        color: ['#555', '#409EFF', '#66ccff'],
      })
    })

    this.subjectColumns = ['math', 'history', 'physical', 'biology', 'geography', 'political']
    this.classOptions = ['', '1', '1', '1']
    this.researchDetailInfo = [
      { id: 1, name: '', class: '1', subject: ['math', 'history', 'physical'] },
      { id: 2, name: '', class: '1', subject: ['biology', 'geography', 'political'] },
    ]
  },
}
</script>

<style lang="less" scoped>
.container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  padding: 0 20px;
  > .el-badge {
    margin: 0 20px;
  }
}
.red {
  color: red;
}
.percent {
  width: 400px;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  margin: 15px 0;
  > div {
    flex-shrink: 0;
  }
  > div[role='progressbar'] {
    flex-grow: 1;
    margin: 0 20px 0 10px;
  }
}
.chartContainer {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 30px;
}
.tableHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 20px;
  > span {
    font-size: 16px;
    font-weight: bold;
  }
}
</style>




