const pickerShortcutClick = (picker, day) => {
  const end = new Date()
  const start = new Date()
  const ms = 3600 * 1000 * 24 * day
  start.setTime(start.getTime() - ms)
  picker.$emit('pick', [start, end])
}

export const studentInfoColumns = [
  { label: '', prop: 'name' },
  { label: '', prop: 'normol' },
  { label: '', prop: 'absent' },
  { label: '', prop: 'late' },
  { label: '', prop: 'leave' },
]

export const pickerOptions = {
  shortcuts: [{
    text: '',
    onClick(picker) {
      pickerShortcutClick(picker, 7)
    },
  }, {
    text: '',
    onClick(picker) {
      pickerShortcutClick(picker, 30)
    },
  }, {
    text: '',
    onClick(picker) {
      pickerShortcutClick(picker, 90)
    },
  }, {
    text: '',
    onClick(picker) {
      pickerShortcutClick(picker, 365)
    },
  },
  ],
}

export const attendanceInfo = [{
  label: '',
  key: 'normol',
  color: '#67C23A',
}, {
  label: '',
  key: 'absent',
  color: '#F56C6C',
}, {
  label: '',
  key: 'late',
  color: '#409EFF',
}, {
  label: '',
  key: 'leave',
  color: '#909399',
},
]

export const chartOptions = {
  title: {
    text: '',
    left: 'center',
    textStyle: {
      fontWeight: 'normal',
    },
  },
  series: [{
    name: '',
    type: 'pie',
    data: [{ value: 20, name: '' }, { value: 80, name: '' }],
    label: {
      normal: {
        show: false,
      },
    },
    labelLine: {
      normal: {
        show: false,
      },
    },
  },
  ],
  color: ['#555', '#409EFF'],
}



